
@import "../component.scss";

.selectbox{
	position: relative;
	display: block;
	height: 40px;
	border: 1px solid #ccc;
	box-sizing: border-box;
	background-color: #fff;
	&:before,
	&:after{
		content: '';
		position: absolute;
		right: 20px;
		display: inline-block;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 4px 8px 4px;
		border-color: transparent transparent #676767 transparent;
	}
	&:before{
		top: 9px;
	}
	&:after{
		bottom: 9px;
		transform: rotate(180deg);
	}
	select{
		width: 100%;
		height: 100%;
		padding-left: 20px;
		padding-right: 60px;
		color: #000;
		font-size: 13px;
		border: none;
		background-color: transparent;
		appearance: none;
		&::-ms-expand {
			display: none;
		}
	}
	&-inline {
		display: inline-block;
	}
}
.input{
	&-inline {
		display: inline-block;
	}
	&-box {
		textarea,
		input{
			width: 100%;
			height: 40px;
			padding: 0 15px;
			//line-height: 40px;
			border: 1px solid #ccc;
			box-sizing: border-box;
			font-size: 13px;
			border-radius: 2px;
			&[readonly] {
				background: {
					color: #f9f9f9;
				}
			}
		}
		textarea {
			min-height: 100px;
			height: auto;
			padding: {
				top: 10px;
				bottom: 10px;
			}
		}
	}
	&-group {
		display: table;
		width: 100%;
		> * {
			display: table-cell;
			&.selectbox,
			&.input-box {
				width: 100%;
				padding-right: 15px;
			}
		}
		.btn {
			padding: {
				right: 20px;
				left: 20px;
			}
		}
	}
	&-upload {
		position: relative;
		display: block;
		padding: {
			right: 110px;
		};
		input {
			cursor: pointer;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 2;
			width: 100%;
			height: 100%;
			border: 0;
			opacity: 0;
		}
		.input-box {
			font-size: 13px;
			display: block;
			height: 40px;
			padding: 0 15px;
			border: 1px solid #ccc;
			border-radius: 2px;
			box-sizing: border-box;
		}
		.value {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			height: 40px;
			padding: 0 15px;
			@include align-middle;
		}
		.btn {
			position: absolute;
			top: 0;
			right: 0;
			width: 96px;
			padding: 0;
		}
	}
}

.form{
	.input-inline,
	.selectbox-inline {
		width: 34.298%;
	}
}


.row-group{
	.input-inline{
		width: 20.66%;
	}
}

.calendar{
	display: inline-block;
	label,
	.btn{
		display: inline-block;
	}
	label{
		margin-right: 5px;
		font-size: 14px;
		color: #333;
	}
	input{
		max-width: 121px;
	}
	.btn{
		padding: 0 20px;
	}
}

